.form {
  @include flex-v-center;

  &-header {
    grid-column: 10 span;
    grid-row: 3;

    &__label {
      @include flex-v-center;

      position: relative;
      width: 100%;
      height: 100%;

      &--svg {
        position: absolute;
        left: 31px;
        margin-right: 6px;
        width: 20px;
        height: 20px;
        fill: var(--grey-color);
      }
    }

    &__input {
      padding-left: 60px;
      padding-right: 36px;
      outline: none;
      border-radius: 10px;
      width: 100%;
      height: 100%;
      background-color: var(--grey_light-color);
      transition: background-color 0.3s ease-in-out;

      &::placeholder {
        font-weight: 400;
        font-size: 16px;
        line-height: 120%;
        color: var(--grey-color);
      }

      &:focus-visible:not(:hover):not(:active) {
        outline: 2px solid var(--primary-color);
        background-color: var(--pink_light-color);
      }

      &:focus + .form-header__label--svg {
        fill: var(--primary-color);
      }

      &:hover {
        outline: none;
        background-color: var(--md_grey-color);
      }
    }
  }
}
