.header {
  background: linear-gradient(var(--grey_light-color) 50px, var(--white-color) 50px 100%);

  &__container {
    grid-template-rows: 50px 91px 60px;
    padding-right: 10px;
  }

  &__phone {
    @include flex-v-center;

    grid-column: 5 span;
    align-self: center;
    width: 145px;
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    color: var(--secondary-color);
    transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

    &--svg {
      width: 22px;
      height: 22px;
      fill: currentColor;
    }

    &:focus:not(:hover):not(:active) {
      outline: none;
      background-color: var(--bone-color);
    }

    &:hover {
      outline: none;
      color: var(--secondary_light-color);
    }

    &:active {
      color: var(--secondary_shade-color);
    }
  }

  &__wrap {
    @include flex-all-end;

    grid-column: 2 span;
    grid-row: 3;
    column-gap: 25px;

    &-user {
      @include flex-all-center;

      padding: 3px 4px;
      width: 34px;
      height: 36px;
      transition: background-color 0.3s ease-in-out;

      &--svg {
        width: 26px;
        height: 28px;
        fill: var(--primary-color);
        transition: fill 0.3s ease-in-out;
      }

      &:focus-visible:not(:hover):not(:active) {
        outline: none;
        background-color: var(--primary_light-color);
      }

      &:hover {
        outline: none;
      }

      &:active {
        outline: none;
      }

      &:focus-visible:not(:hover):not(:active) .header__wrap-user--svg {
        fill: var(--white-color);
      }

      &:hover .header__wrap-user--svg {
        fill: var(--primary_light-color);
      }

      &:active .header__wrap-user--svg {
        fill: var(--primary_shade-color);
      }
    }

    &-basket {
      @include flex-v-center;

      padding: 3px 4px;
      width: 49px;
      height: 34px;
      transition: background-color 0.3s ease-in-out;

      &--quantity {
        @include flex-all-center;

        border-radius: 100%;
        width: 14px;
        height: 15px;
        font-weight: 700;
        font-size: 10px;
        line-height: 100%;
        color: var(--white-color);
        background-color: var(--secondary-color);
      }

      &--svg {
        width: 30px;
        height: 28px;
        fill: var(--primary-color);
        transition: fill 0.3s ease-in-out;
      }

      &:focus-visible:not(:hover):not(:active) {
        outline: none;
        background-color: var(--primary_light-color);
      }

      &:hover {
        outline: none;
      }

      &:active {
        outline: none;
      }

      &:focus-visible:not(:hover):not(:active) .header__wrap-basket--svg {
        fill: var(--white-color);
      }

      &:hover .header__wrap-basket--svg {
        fill: var(--primary_light-color);
      }

      &:active .header__wrap-basket--svg {
        fill: var(--primary_shade-color);
      }
    }
  }

  &__btn-dropdown {
    position: absolute;
    right: 36px;
    border-radius: 100px;
    border: 2px solid var(--primary-color);
    width: 32px;
    height: 32px;
    transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;

    &:focus-visible:not(:hover):not(:active) {
      outline: none;
      background-color: var(--primary_light-color);
    }

    &:hover {
      outline: none;
      background-color: var(--primary-color);
    }

    &:active {
      outline: none;
      border-color: var(--primary_shade-color);
      background-color: var(--primary_shade-color);
    }

    &:focus-visible:not(:hover):not(:active) .header__btn-dropdown--svg {
      stroke: var(--white-color);
    }

    &:hover .header__btn-dropdown--svg {
      stroke: var(--white-color);
    }

    &:active .header__btn-dropdown--svg {
      stroke: var(--white-color);
    }

    &--svg {
      width: 8px;
      height: 28px;
      stroke-width: 2px;
      stroke: var(--primary-color);
      fill: transparent;
      transition: stroke 0.3s ease-in-out;
    }
  }
}

.logo {
  grid-column: 2 span;
  grid-row: 2;
  align-self: center;
}
