.nav {
  &-top {
    grid-column: 7 span;
    justify-self: right;
    align-self: center;

    &__list {
      display: flex;
      column-gap: 54px;
    }

    &__link {
      padding: 2px 5px;
      font-size: 14px;
      font-weight: 400;
      line-height: 100%;
      color: var(--grey-color);
      transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

      &:focus:not(:hover):not(:active) {
        outline: none;
        background-color: var(--pink_violet-color);
      }

      &:hover {
        outline: none;
        color: var(--primary-color);
      }

      &:active {
        color: var(--primary_shade-color);
      }
    }
  }

  &-main {
    grid-column: 10 span;
    grid-row: 2;
    justify-self: right;
    align-self: center;

    &__list {
      display: flex;
      column-gap: 82px;
    }

    &__link {
      padding: 2px 5px;
      font-weight: 400;
      font-size: 16px;
      line-height: normal;
      color: var(--black-color);
      transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;

      &:focus:not(:hover):not(:active) {
        outline: none;
        background-color: var(--pink_violet-color);
      }

      &:hover {
        outline: none;
        color: var(--primary-color);
      }

      &:active {
        color: var(--primary_shade-color);
      }
    }
  }
}
