// colors
:root {
  // base
  --font-family: 'OpenSans', sans-serif;
  --second-family: 'PlayfairDisplay', sans-serif;
  --content-width: 1280px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --white-color: #fff;
  --black-color: #333333;
  --bone-color: #fff5d1;
  --green_input-color: #b8ec64;
  --gold-color: #d5b986;
  --grey-color: #999999;
  --grey_light-color: #f6f6f6;
  --grey_shade-color: #666666;
  --md_grey-color: #eaeaea;
  --pink_light-color: #f8efff;
  --pink_violet-color: #ebd1ff;
  --primary-color: #a65cf0;
  --primary_light-color: #c998fb;
  --primary_shade-color: #7033ac;
  --secondary-color: #ff862f;
  --secondary_light-color: #ffbd59;
  --secondary_shade-color: #bb370e;
}
